<template>
    <div class="all_com">
        <div class="title_com">优惠活动管理</div>
        <el-card class="box-card" :body-style="{'background-color': '#ffffff'}">
            <div slot="header" class="clearfix">
                <span>优惠活动列表</span>
            </div>
            <el-table :data="tableData" stripe style="width: 100%;height:100%">
                <el-table-column prop="ID" label="Id" width="60">
                </el-table-column>
                <el-table-column prop="Name" label="优惠码名">
                    
                </el-table-column>
                <el-table-column prop="EnglishName" label="优惠码英文名">
                </el-table-column>
                <el-table-column prop="Type" label="优惠码类型">
                    
                    <template slot-scope="scope">
                        {{couponType[scope.row.Type]}}
                    </template>
                </el-table-column>
                <el-table-column prop="StartTime" label="开始日期">
                    <template slot-scope="scope">
                        {{scope.row.StartTime | date}}
                    </template>
                </el-table-column>
                <el-table-column prop="EndTime" label="结束日期">
                    <template slot-scope="scope">
                        {{scope.row.EndTime | date}}
                    </template>
                </el-table-column>
                <el-table-column prop="ConditionPrice" label="达成条件价格">
                    
                </el-table-column>
                <el-table-column prop="Discount" label="折扣">
                   
                </el-table-column>
                <el-table-column prop="UsdConditionPrice" label="美元达成条件价格">
                    
                </el-table-column>
                <el-table-column prop="UsdDiscount" label="USD折扣">
                    
                </el-table-column>
                <el-table-column prop="BtcDiscount" label="BTC折扣">
                    
                </el-table-column>
            </el-table>
            <el-pagination class='page' :current-page.sync="pageNo" @current-change='getList' background layout="prev, pager, next" :total="totalNo">
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
import {  couponType } from "@/fetch/const";
export default {
    name: "",
    data() {
        return {
            tableData: [],
            status: -1,
            couponType: couponType,
            pageNo: 1,
            totalNo: 0,
            input: ""
        };
    },
    methods: {
        getList() {
            let params = {
                status: this.status,
                pageNo: this.pageNo - 1,
                pageSize: 10
            };
            this.$api("/admin/coupon/activity_list", params).then(res => {
                this.tableData = res.data.list;
                this.totalNo = res.data.page.totalNum;
            });
        },
        search() {
            this.pageNo = 1;
            this.getList();
        },
        Detail(id) {
            this.$router.push(`/productmanage/product_detail/${id}`);
        }
    },
    created() {
        this.getList();
    }
};
</script>
<style lang='scss' scoped>
@import "../../assets/css/titlecom.scss";
.detaila:hover {
    color: #409eff;
    text-decoration: underline;
}
.search_fun {
    height: 60px;
    position: relative;
    top: 10px;
    float: right;
}
.search_input {
    width: 200px;
}
</style>


